import MescrollVue from "mescroll.js/mescroll.vue";
import "mescroll.js/mescroll.min.css";
import { selectService } from "../../server/employee";
export default {
  name: "selectService",
  components: { MescrollVue },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll: null,
      mescrollDown: {
        callback: this.downCallBack,
        clearEmptyId: "main",
        isBoth: false,
        isBounce: true
      },
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.getList,
        htmlNodata: '<p class="upwarp-nodata">到底啦~~~</p>',
        noMoreSize: 5,
        page: {
          num: 0,
          size: 50,
          total: 0
        },
        empty: {
          warpId: "main",
          icon: "./static/mescroll/mescroll-empty.png",
          tip: "暂无相关数据~" //提示
        },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>'
      },
      pages: {
        page: 1,
        size: 50,
        total: 0
      },
      dataList: [], // 列表数据
      placeholder:'请输入关键字'
    };
  },
  created() {},
  watch: {
    value: function (newval,oldval) {
      if (this.value == "") {
        this.placeholder='请输入关键字'
        this.handleSeach()
      }else{
        this.placeholder=''
      }
    }
  },
  methods: {
    handleCommentFocus(){
      this.placeholder=''
    },
    search(){
      this.handleSeach()
    },
    downCallBack(mescroll) {
      setTimeout(function() {
        mescroll.endSuccess();
      }, 1500);
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll;
    },
    getList(page, mescroll) {
      const that = this;
      selectService({
        page: page.num,
        limit: page.size,
        organizationName: this.value
      }).then(e => {
        // console.log(page.num);
        if (e.data.code == 200) {
          var arr = e.data.data.list;
          let data = page.num == 1 ? [] : this.dataList;
          this.dataList = this.dataList.concat(arr);
          //  console.log(this.dataList);
          this.$nextTick(() => {
            this.mescroll.endSuccess(arr.length);
          });
        }
      });
    },
    handleSeach() {
      let searchList = [];
      const that = this;
      selectService({
        page: this.pages.page,
        limit: this.pages.size,
        organizationName: this.value
      }).then(e => {
        // console.log(page.num);
        if (e.data.code == 200) {
          this.searchList = e.data.data.list;
          this.dataList = this.searchList;
        }
      });
    },
    handleLink(id, organizationName) {
      localStorage.setItem("organizationName",JSON.stringify(organizationName));
      localStorage.setItem("id", JSON.stringify(id));
      this.$router.push({
        path: "/AddEmployee",
        query: {
          id: id
        }
      });
    }
  }
};



